<template>

    <div id="Songrow" class="">
      <!-- Simple view of rows -->
      <div :class="[{ 'active' : simpleView}, 'simpleView', 'row', 'align-items-center']">
        <div class="col-1 "><span class="sno">2</span></div>

        <div class="col">
          <h2>{{ songName }}</h2>
          <p>{{ artist }}</p>
        </div>
      </div>

      
    </div>

</template>
<script>
export default {
  name: "Songrow",
  data: () => ({

    imgUrl : 'https://static.jatt.link/thumbs/100_100/bzcai.jpg',
    songName : 'High Rated Gabru (Original)',
    artist : 'Guru Randhawa',
    file : 'https://dl.jatt.link/hd1.jatt.link/baabd7548082c946d2e457fc879e53dd/gzbzv/High%20Rated%20Gabru-(Mr-Jatt.com).mp3'
  })
}
</script>
<style lang="scss" scoped>
#Songrow{
  padding: 5px 0;
  border-bottom: solid 1px rgba(0,0,0,0.1);

  .sno{
    color: rgba(0,0,0,0.5);
    background-color: rgba(0,0,0,0.08);
    border-radius: 50%;
    font-size: 12px;
    line-height: 20px;
    width: 20px; height: 20px;
    display: inline-block; text-align: center;
  }
  .coverImage{
    border-radius: 50px;
  }
  h2{
    margin: 0; padding: 0;
    font-size: 18px;
    font-weight: 100;
  }
  p{
    margin: 0; padding: 0;
    font-size: 14px;
    color: rgba(0,0,0,0.4);
  }
}

</style>



